import Vue from 'vue'
import App from './App.vue'
import vuetify from '@/plugins/vuetify' // path to vuetify export
import VueRouter from 'vue-router'
import router from './router'


Vue.use(VueRouter)
Vue.config.productionTip = false

new Vue({
  router,
  vuetify,
render: h => h(App)
}).$mount('#app')
